import React from "react";
const twitch_icon = require("../../Assets/twitch_icon.svg");
const twitch_banner = require("../../Assets/Capas/Capa TW.png");
const public_child = require("../../Assets/Icons/Publico - Jovem v3.png");
const money = require("../../Assets/Icons/Money.png");
const partners = require("../../Assets/Icons/Parceiros.png");

export default function TwitchWin({ name }) {
  return (
    <div className="container go-up">
      <div className="game-content bigger">
        <div className="platform-win">
          <h3>{name}, sua plataforma ideal é a</h3>
          <div>
            <p className="title">Twitch TV</p>
            <img
              src={twitch_icon}
              height="200px"
              width="200px"
              className="ml-3"
              alt="banner"
            />
          </div>
          <p className="mt-4 justify-text">
            A Twitch é uma plataforma solidificada na área de streaming, e tem
            ferramentas, como chat ao vivo com stickers personalizados criados
            pelo produtores de conteúdo, que proporcionam contato direto com o
            seu público alvo, especificamente o público “gamer” que está
            bastante presente por lá. A plataforma também oferece diversas
            formas desse público te apoiar como criador de conteúdo, o sistema
            de inscrições cria um público fiel ao produtor, com o seu
            crescimento nessa plataforma é possível até conseguir parcerias e
            patrocínios com diversas marcas e empresas do meio digital que estão
            cada vez mais interessadas em apoiar a onda dos games na internet.
          </p>
          <div>
            <p className="title mt-4">
              Por que a Twitch é a melhor plataforma para você
            </p>
            <div className="info-content">
              <div className="info-container">
                <p className="info-title">Monetização</p>
                <img
                  style={{
                    width: 150,
                    height: 150
                  }}
                  src={money}
                  alt="banner"
                />
                <p className="info-text">
                  A monetização se dá através de doações, bits (moeda do próprio
                  site), e alguns tipos de inscrições em canais de produtores de
                  conteúdo. É possível também fazer o uso de propagandas e
                  anúncios na plataforma.
                </p>
              </div>
              <div className="info-container">
                <p className="info-title">Público alvo</p>
                <img
                  style={{
                    width: 150,
                    height: 150
                  }}
                  src={public_child}
                  alt="banner"
                />
                <p className="info-text">
                  O público alvo da twitch é majoritariamente o público “gamer”
                  interessados em conteúdo e interações em tempo real. Pessoas
                  que consomem e-sports, reacts e gameplays encontram uma gama
                  diversa de conteúdos e pessoas nessa plataforma.
                </p>
              </div>
              <div className="info-container">
                <p className="info-title">Parcerias</p>
                <img
                  style={{
                    width: 150,
                    height: 150
                  }}
                  src={partners}
                  alt="banner"
                />
                <p className="info-text">
                  Para ter uma chance de virar parceiro da plataforma é
                  necessário streamar com uma certa frequência e ter um mínimo
                  de 75 views como média dos últimos 30 dias. Por conta do seu
                  tipo de conteúdo, parcerias e anúncios de marcas de tecnologia
                  e games são muito comuns no site.
                </p>
              </div>
            </div>
          </div>
          <div>
            <p className="title mt-4">Uma informação importante</p>
            <p className="info-title">Para parceiros Twitch</p>
            <p>
              Para inscrição num canal e emotes: os valores são 4,99, 9,99,
              24,99 dólares (destrava 50 emotes). Este valor é divido entre o
              criador e a plataforma em 50% para cada.
            </p>
            <p>
              <b>Bits:</b> unidade que a audiência usa para apoiar o criador de
              conteúdo. Twitch ganha um centavo de dólar em cima de cada Bit.
            </p>
            <p>
              <b>Anúncios/Propagandas:</b> parceiros podem determinar a duração
              e a frequência com que anúncios aparecem em seu canal, ganhando em
              cima de cada anúncio que é exibido. Geralmente de 1 a 2 dólares a
              cada 1000 visualizações, mas varia de acordo com a quantidade de
              cliques que o parceiro possui.
            </p>
            <p className="info-title">
              Algumas pessoas que escolheram essa plataforma
            </p>
            <div>
              <img className="banner" src={twitch_banner} alt="banner" />
            </div>
            <p className="mt-4 justify-text">
              Yuna Adora “streamar” suar partidas ranqueadas de League of
              Legends, com o aumento do seu “elo” no jogo, começou a notar que
              tinha um público interessado e decidiu usar a TwitchTV para lhe
              render uma grana extra no final do mês, no futuro, pretende fazer
              parcerias com empresas relacionadas ao e-sport.
            </p>
          </div>
          <div>
            <p className="info-title mt-4">Fontes/Referências</p>
            <p className="justify-text fonts">
              <i>
                https://www.twitch.tv/p/en/stream/
                <br />
                https://affiliate.twitch.tv/
                <br />
                https://www.twitch.tv/p/partners/
                <br />
                https://twitchmeta.com/understanding-the-4-key-areas-of-monetization-on-twitch-and-how-to-maximize-your-earnings
                <br />
              </i>
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}
