import React from "react";
import "./FacebookWin.css";
const facebook_icon = require("../../Assets/facebook_icon.svg");
const facebook_banner = require("../../Assets/Capas/Capa FB.png");
const public_child = require("../../Assets/Icons/Publico - Jovem v3.png");
const money = require("../../Assets/Icons/Money.png");
const partners = require("../../Assets/Icons/Parceiros.png");

export default function FacebookWin({ name }) {
  return (
    <div className="container go-up">
      <div className="game-content bigger">
        <div className="platform-win">
          <h3>{name}, sua plataforma ideal é o</h3>
          <div>
            <p className="title">Facebook</p>
            <img
              src={facebook_icon}
              height="200px"
              width="200px"
              className="ml-3"
              alt="banner"
            />
          </div>
          <p className="mt-4 justify-text">
            A plataforma é uma das maiores redes sociais do momento e oferece
            uma grande facilidade no engajamento do público, assim também como
            facilidade de aquisição de novos visitantes no seu conteúdo. Além
            disso, ela conta com alguns contratos de exclusividades com
            conteúdos de games e esportes, atraindo um grande público a acessar
            e consumir vídeos na plataforma, possibilitando maior chance de
            atingir o seu público alvo. Esses pontos são alguns dos fatores que
            podem ajudar você a alcançar um público maior e ter mais
            visibilidade no seu conteúdo.
          </p>
          <div>
            <p className="title mt-4">
              Por que o Facebook é a melhor plataforma para você
            </p>
            <div className="info-content">
              <div className="info-container">
                <p className="info-title">Monetização</p>
                <img
                  style={{
                    width: 150,
                    height: 150
                  }}
                  src={money}
                  alt="banner"
                />
                <p className="info-text">
                  A monetização no Facebook se dá através de incluir ads nos
                  vídeos, adicionando inscrições pagas a sua página ou grupo, ou
                  através de colaborações com outra marcas.
                </p>
              </div>
              <div className="info-container">
                <p className="info-title">Público alvo</p>
                <img
                  style={{
                    width: 150,
                    height: 150
                  }}
                  src={public_child}
                  alt="banner"
                />
                <p className="info-text">
                  Público mais abrangente, criando mais comodidade, podendo ter
                  acesso ao vídeo sem a necessidade sair da plataforma social. O
                  uso do humor, memes e correntes, são interações que geram
                  muita comoção.
                </p>
              </div>
              <div className="info-container">
                <p className="info-title">Parcerias</p>
                <img
                  style={{
                    width: 150,
                    height: 150
                  }}
                  src={partners}
                  alt="banner"
                />
                <p className="info-text">
                  Para tornar-se parceiro e iniciar a monetização de um vídeo
                  com anúncios in-stream o criador necessita ter no mínimo 10
                  mil seguidores, e 30 mil visualizações de no mínimo um minuto.
                </p>
              </div>
            </div>
          </div>
          <div>
            <p className="title mt-4">Uma informação importante</p>
            <p>
              Você pode checar a elegibilidade para monetização da sua página
              através{" "}
              <a href="https://business.facebook.com/creatorstudio/">
                deste link.
              </a>
            </p>
            <p className="info-title">Pagamento</p>
            <p>
              O pagamento no Facebok é feito através do banco ou através do
              PayPal (Os pagamentos serão transferidos para a conta vinculada
              quando se acumular um valor mínimo de receita de US$ 100).
            </p>
            <p className="info-title">
              Algumas pessoas que escolheram essa plataforma
            </p>
            <div>
              <img className="banner" src={facebook_banner} alt="banner" />
            </div>
            <p className="mt-4 justify-text">
              Jack gosta de produzir vlogs, faz postagens engraçadas no
              Instagram, gosta de criar conteúdo relacionado a memes, tem um
              linguajar que por vezes é impróprio para pessoas menores de 18
              anos, não faz questão de produzir vídeos sérios ou algum tipo de
              conteúdo mais organizado. Escolheu aderir ao Facebook para criar
              vídeos curtos e engraçados.
            </p>
          </div>
          <div>
            <p className="info-title mt-4">Fontes/Referências</p>
            <p className="justify-text fonts">
              <i>
                https://www.facebook.com/help/publisher/content-guidelines
                <br />
                https://www.facebook.com/business/learn/lessons/how-make-money-facebook?ref=mphc_lwe
                <br />
                https://www.facebook.com/help/publisher/monetization-eligibility
              </i>
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}
